import {
  AppRoot,
  SplitLayout,
  SplitCol,
  Panel,
  PanelHeader,
  Header,
  Group,
  SimpleCell,
  usePlatform,
  useAdaptivityConditionalRender,
  Cell,
  Platform,
  Tabbar,
  TabbarItem,
  Counter,
  Badge,
  PanelHeaderBack,
  Placeholder,
  PanelHeaderButton,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";
import { View, Root, Match, Epic, replace, back } from '@itznevikat/router';
import { News } from "./pages/News";
import { useState } from "react";
import { Icon28ChevronBack, Icon28NewsfeedOutline, Icon28PlaneOutline, Icon28ServicesOutline, Icon28SettingsOutline, Icon28UserCircleOutline, Icon56NewsfeedOutline } from "@vkontakte/icons";

const App = () => {
  const platform = usePlatform();
  const { viewWidth } = useAdaptivityConditionalRender();
  const [activeStory, setActiveStory] = useState("news");
  const isVKCOM = platform !== Platform.VKCOM;

  function gotomain() {
    replace('/');
  }

  const activeCell = {
    backgroundColor: "var(--vkui--color_background_secondary)",
    borderRadius: 12,
  };
  const disabledCell = {
    backgroundColor: "var(--vkui--color_background_content)",
    borderRadius: 12,
  };

  return (
    <AppRoot>
      <SplitLayout header={<PanelHeader separator={false} />}>
        <Match>
          <SplitLayout
            header={<PanelHeader separator={false} />}
            style={{ justifyContent: "center" }}>
            <SplitCol width="100%" maxWidth="700px" stretchedOnMobile autoSpaced>
              <Root nav="/">
                <View nav="/">
                  <News nav="/" />
                </View>
                <View nav="/persik">
                  <Panel nav="/">
                    <PanelHeader before={<PanelHeaderBack onClick={gotomain} />} aria-label="press me">
                      SPACE
                    </PanelHeader>
                    meow
                  </Panel>
                </View>
                <View nav="/facts">
                  <Panel nav="/">
                    <PanelHeader>SPACE</PanelHeader>
                    Hello
                  </Panel>
                </View>
                <View nav="/settings">
                  <Panel nav="/">
                    <PanelHeader>Настройки</PanelHeader>
                  </Panel>
                </View>
              </Root>
            </SplitCol>
            {viewWidth.tabletPlus && (
              <SplitCol
                className={viewWidth.tabletPlus.className}
                fixed
                width={280}
                maxWidth={280}
              >
                <Panel>
                  {isVKCOM && <PanelHeader />}
                  <Group>
                    <Cell
                      disabled={activeStory === "news"}
                      style={activeStory === "news" ? activeCell : disabledCell}
                      onClick={() => {
                        setActiveStory("news");
                        replace('/');
                      }}
                      before={<Icon28NewsfeedOutline />}
                    >
                      Новости
                    </Cell>
                    <Cell
                      disabled={activeStory === "facts"}
                      style={activeStory === "facts" ? activeCell : disabledCell}
                      onClick={() => {
                        setActiveStory("facts");
                        replace('/facts');
                      }}
                      before={<Icon28PlaneOutline />}
                    >
                      Факты
                    </Cell>
                    <Cell
                      disabled={activeStory === "settings"}
                      style={activeStory === "settings" ? activeCell : disabledCell}
                      onClick={() => {
                        setActiveStory("settings");
                        replace('/settings');
                      }}
                      before={<Icon28SettingsOutline />}
                    >
                      Настройки
                    </Cell>
                  </Group>
                </Panel>
              </SplitCol>
            )}
          </SplitLayout>
        </Match>

      </SplitLayout >
    </AppRoot >
  );
};
export default App;



